import React from 'react';
import { CDBModalFooter, CDBBtn, CDBIcon, CDBBox, CDBPageLink} from 'cdbreact';
import './Footer.css'
function Footer() {
  return (
    <CDBModalFooter className="footer shadow">
      <CDBBox display="flex" flex="column" className="mx-auto py-5" style={{ width: '90%' }}>
        <CDBBox display="flex" justifyContent="between" className="flex-wrap">
          <CDBBox alignSelf="center">
            <a href="/" className="d-flex align-items-center p-0 text-dark">
              <img alt="logo" src="logo" width="30px" />
              <span className="ms-3 h5 font-weight-bold">Devwares</span>
            </a>
            <CDBBox className="mt-5" display="flex">
              <CDBBtn flat color="dark" className="p-2">
                <CDBIcon fab icon="facebook-f" />
              </CDBBtn>
              <CDBBtn flat color="dark" className="mx-3 p-2">
                <CDBIcon fab icon="twitter" />
              </CDBBtn>
              <CDBBtn flat color="dark" className="p-2">
                <CDBIcon fab icon="instagram" />
              </CDBBtn>
            </CDBBox>
          </CDBBox>
          <CDBBox>
            <p className="h5 mb-4" style={{ fontWeight: '600' }}>
              Devwares
            </p>
            <CDBBox display="flex" flex="column" style={{ cursor: 'pointer' }}>
              <CDBPageLink href="/resoursces">Resources</CDBPageLink>
              <CDBPageLink href="/about">About Us</CDBPageLink>
              <CDBPageLink href="/contact">Contact</CDBPageLink>
              <CDBPageLink href="/blog">Blog</CDBPageLink>
            </CDBBox>
          </CDBBox>
          <CDBBox>
            <p className="h5 mb-4" style={{ fontWeight: '600' }}>
              Products
            </p>
            <CDBBox display="flex" flex="column" style={{ cursor: 'pointer' }}>
              <CDBPageLink href="/windframe">Windframe</CDBPageLink>
              <CDBPageLink href="/Loop">Loop</CDBPageLink>
              <CDBPageLink href="/Cpntrast">Contrast</CDBPageLink>
            </CDBBox>
          </CDBBox>
          <CDBBox>
            <p className="h5 mb-4" style={{ fontWeight: '600' }}>
              Help
            </p>
            <CDBBox display="flex" flex="column" style={{ cursor: 'pointer' }}>
              <CDBPageLink href="/">Support</CDBPageLink>
              <CDBPageLink href="/">Sign Up</CDBPageLink>
              <CDBPageLink href="/">Sign In</CDBPageLink>
            </CDBBox>
          </CDBBox>
        </CDBBox>
        <small className="text-center mt-5">&copy; Devwares, 2024. All rights reserved.</small>
      </CDBBox>
    </CDBModalFooter>
  );
};

export default Footer;